}

.DM-Page .dmScreen {
    display: flex;
    justify-content: center;
    margin: 1rem 0rem;
}

.DM-Page iframe {
    max-width: 90vw;
    max-height: 90vh;
}

.DM-Page .target {
    width: 50%;
