    border: 1px solid grey;
}

.DM-Page .item-container {
    width: 40%;
}

.DM-Page .item {
    padding: 10px;
    border: 1px solid grey;
    margin-bottom: 20px;
    color: white;
}

.DM-Page .newCharacterInput {
    margin-top: 2rem;
}

.DM-Page .buttonCol {
    padding-left: 2.5rem;
}

.DM-Page .tracker {
    background-color: rgba(42, 23, 44, 0.8);
    margin: 1rem 3rem;
}

.DM-Page textarea {
    width: 100rem;
    height: 20rem;
    max-width: 40vw;
}
